/**
 * Rem-converter mixin to convert px to rem
 */
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sup,
sub,
tt,
var,
b,
u,
i,
center,
ul,
ol,
li,
dl,
dt,
dd,
table,
tr,
th,
td,
thead,
tbody,
tfoot,
caption,
form,
fieldset,
legend,
input,
textarea,
select,
label,
applet,
object,
iframe,
audio,
video,
canvas,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary {
  font-size: 100%;
  font-family: inherit;
  font-weight: inherit;
  font-style: inherit;
  line-height: inherit;
  vertical-align: baseline;
  border: 0;
  outline: 0;
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}

audio:not([controls]) {
  display: none;
}

canvas {
  -ms-touch-action: double-tap-zoom;
}

[hidden] {
  display: none;
}

ul,
ol {
  list-style: none;
}

table {
  table-layout: auto;
  border-collapse: separate;
  border-spacing: 0;
  empty-cells: hide;
}

a {
  text-decoration: none;
}

body {
  font-family: "Exo 2", sans-serif;
  min-width: 320px;
}

header {
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.07);
  padding-bottom: 27px;
  margin-bottom: 80px;
}

.container {
  width: 1160px;
  margin: 0 auto;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
textarea {
  -webkit-appearance: none;
}

button,
input,
select,
textarea {
  vertical-align: baseline;
  *vertical-align: middle;
  -webkit-border-radius: 0;
}

button,
input {
  line-height: normal;
  *overflow: visible;
}

table button,
table input {
  *overflow: auto;
}

button,
input[type=button],
input[type=reset],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: button;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

input[type=search] {
  -webkit-appearance: textfield;
}

input[type=radio] {
  -webkit-appearance: radio;
}

input[type=checkbox] {
  -webkit-appearance: checkbox;
}

input::-ms-clear {
  display: none;
}

input[type=password]::-ms-reveal {
  display: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

* html .clearfix {
  zoom: 1;
}

*:first-child + html .clearfix {
  zoom: 1;
}

.cleaner {
  height: 0;
  line-height: 0;
  clear: both;
}

select.header_phone {
  -webkit-appearance: none;
  -moz-appearance: none;
  background: transparent;
  background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
