.top_header {
  background-color: #333333;
  height: 40px;
  display: flex;
  align-items: center;
  color: #ffffff;
}
.top_header nav {
  float: left;
  height: 40px;
  display: flex;
  width: 70%;
}
.top_header nav ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.top_header nav ul li {
  position: relative;
  margin-right: 45px;
}
.top_header nav ul li:last-child {
  margin-right: 0;
}
.top_header nav ul li > a {
  color: #ffffff;
  text-transform: uppercase;
  position: relative;
  display: block;
  font-size: 12px;
  font-size: 0.75rem;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.top_header nav ul li > a:hover {
  color: #5aa1e1;
}
.top_header nav ul li > a span:after {
  width: 0;
  height: 0;
  border: 0.313em solid transparent;
  /* 5 */
  border-bottom: none;
  border-top-color: #ffffff;
  content: "";
  vertical-align: middle;
  display: inline-block;
  position: relative;
  right: -0.313em;
  /* 5 */
}
.top_header nav ul li > ul {
  display: none;
  flex-direction: column;
  width: 210px;
  height: auto;
  padding: 10px 15px;
  position: absolute;
  top: 200%;
  left: -20px;
  z-index: 30;
